import {useState} from 'react';
function Session37(){
    const [name,setname] = useState("")
    const [email,setemail] = useState("")
    const [phone,setphone] = useState(0)
    return(
        <>
        <h1>React Forms</h1>
        <form>
            <label>Name:</label>
            <input value={name} onChange={(e)=>setname(e.target.value)} type="text"/><br/><br/>
            <label>Email:</label>
            <input value={email} onChange={(e)=>setemail(e.target.value)} type="email"/><br/><br/>
            <label>Phone:</label>
            <input value={phone} onChange={(e)=>setphone (e.target.value)} type="number"/><br/><br/>
        </form>
        <h2>User Details:</h2>
        <h3>Name: {name}</h3>
        <h3>Email: {email}</h3>
        <h3>Phone: {phone}</h3>
        </>
    )
}
export default Session37