import {useState, useEffect } from "react";

function Session40(){
     
    const[error,seterror] = useState(null);
    const[isLoaded,setisLoaded] = useState(false);
    const[data,setdata] = useState([]);

    useEffect(()=>{
        const axios = require('axios');
        axios.get("https://icanhazdadjoke.com/slack")
        .then((response)=>{
            setisLoaded(true);
            setdata(response.data.attachments[0].text);
        })
        .catch((error)=>{
            setisLoaded(true);
            seterror(error);
        })    
        .then(
        console.log("no matter what I will run!") )
    },[])
    return(
        <>
        <h1>Axios</h1>
        <h2>{data}</h2>
        </>
    )
}
export default Session40;